import Particle from './Particle';
import Popup from './popup';
import { asteroidVertices, randomNumBetween } from './helpers';
import STORE from './STORE'

export default class Asteroid {

  constructor(args) {

    this.position = args.position
    this.velocity = {
      x: randomNumBetween(-1.5, 1.5),
      y: randomNumBetween(-1.5, 1.5)
    }
    this.rotation = 0;
    this.rotationSpeed = randomNumBetween(-1, 1)
    this.radius = args.size;
    this.score = (80/this.radius)*5;
    this.create = args.create;
    this.addScore = args.addScore;
    this.vertices = asteroidVertices(8, args.size)
    this.isChunk = args.isChunk
    this.theAsteroidData = args.theAsteroidData

  }

  destroy(){

    this.delete = true;
    this.addScore(this.score);

    // Explode
    for (let i = 0; i < this.radius; i++) {
      const particle = new Particle({
        lifeSpan: randomNumBetween(60, 100),
        size: randomNumBetween(1, 6),
        position: {
          x: this.position.x + randomNumBetween(-this.radius/4, this.radius/4),
          y: this.position.y + randomNumBetween(-this.radius/4, this.radius/4)
        },
        velocity: {
          x: randomNumBetween(-1.5, 1.5),
          y: randomNumBetween(-1.5, 1.5)
        }
      });
      this.create(particle, 'particles');
    }

    // Break into smaller asteroids
    if(this.radius > 10){
      for (let i = 0; i < 2; i++) {
        let asteroid = new Asteroid({

          size: this.radius/2,
          position: {
            x: randomNumBetween(-10, 20)+this.position.x,
            y: randomNumBetween(-10, 20)+this.position.y
          },
          create: this.create.bind(this),
          addScore: this.addScore.bind(this),
          isChunk: true
        });
        this.create(asteroid, 'asteroids');
      }
    }

    if(true){

      const popup = new Popup({

        lifeSpan: 100,
        size: 0,
        position: {
          x: this.position.x ,
          y: this.position.y 
        },
        theAsteroidData: this.theAsteroidData,
        addScore: this.score,
        isChunk: this.isChunk,
        velocity: {
          x: randomNumBetween(-.2, .2),
          y: randomNumBetween(-.2, .2)
        }


      });

      this.create(popup, 'particles');

    }

    if(this.isChunk === false){
      STORE._set({recentlyDestroyed: this.theAsteroidData})
      const popup = new Popup({

        lifeSpan: 500,
        size: 0,
        position: {
          x: this.position.x ,
          y: this.position.y+2
        },
        theAsteroidData: this.theAsteroidData,
        addScore: this.score,
        isChunk: this.isChunk,
        velocity: {
          x: randomNumBetween(-.2, .2),
          y: randomNumBetween(-.2, .2)
        }


      });

      this.create(popup, 'particles');

    }


    }

  

  render(state){
    // Move
    this.position.x += this.velocity.x;
    this.position.y += this.velocity.y;

    // Rotation
    this.rotation += this.rotationSpeed;
    if (this.rotation >= 360) {
      this.rotation -= 360;
    }
    if (this.rotation < 0) {
      this.rotation += 360;
    }

    // Screen edges
    if(this.position.x > state.screen.width + this.radius) this.position.x = -this.radius;
    else if(this.position.x < -this.radius) this.position.x = state.screen.width + this.radius;
    if(this.position.y > state.screen.height + this.radius) this.position.y = -this.radius;
    else if(this.position.y < -this.radius) this.position.y = state.screen.height + this.radius;


    const context = state.context;
    context.save();
    context.translate(this.position.x, this.position.y);
    context.rotate(this.rotation * Math.PI / 180);
    context.strokeStyle = '#FFF';
    
    if(this.isChunk === false){
        
        // context.fillStyle = "white"
        // context.font = "1.5rem Arial"
        // context.fillText(this.theAsteroidData.name,10,20)
        // context.textAlign = "center"
        // context.fill()
    
    }
    if(this.isChunk === true){
      
    }
    
    context.lineWidth = 2;
    context.beginPath();
    context.moveTo(0, -this.radius);
    for (let i = 1; i < this.vertices.length; i++) {
      context.lineTo(this.vertices[i].x, this.vertices[i].y);
    }
    context.closePath();
    
    
    context.stroke();
    context.restore();

  }
}



